  pointer-events: none;
}
.modal {
  position: absolute;
  top: 13%;
  left: 3%;
  -webkit-transform: scale(1) rotate(0);
  transform: scale(1) rotate(0);
  width: 100%;
  background-color: #fff;
  -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12),
